@charset "UTF-8";
@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

.modal {
  display: none;
  position: fixed;
  z-index: 50;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(255, 255, 255, 0.96);
  opacity: 0,5%; }

.modal-content {
  position: absolute;
  top: 0;
  right: 0px;
  width: 320px;
  box-shadow: 16px 0px 52px 0px #454F7E05;
  box-shadow: -2px 0px 4px 0px #0E185033;
  background-color: #FFFFFF; }
  .modal-content h2 {
    font-family: TT Lakes;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6000000238418579px;
    text-align: left;
    margin: 60px 0 0 0;
    padding: 0px 0 0 15px;
    color: #1B1C21;
    border-left: 4px solid #41F6D7; }
  .modal-content form {
    display: flex;
    flex-direction: collom;
    /*     border: 2px solid gold; */
    flex-direction: column;
    padding: 0 25px 0 25px; }
    .modal-content form input {
      height: 48px;
      border: 1px solid #EAEAEA;
      border-radius: 6px;
      outline: none;
      /* Убираем стандартное выделение */
      font-family: TT Lakes;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      padding: 0 0 0 10px;
      margin: 25px 0; }
    .modal-content form input::-moz-placeholder {
      font-family: TT Lakes;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      text-align: left;
      /*       padding: 0 0 0 10px; */ }
    .modal-content form input::placeholder {
      font-family: TT Lakes;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      text-align: left;
      /*       padding: 0 0 0 10px; */ }

.close-button {
  transition: transform 1s;
  /*     border: 2px solid violet; */
  position: relative;
  transform: translate(50%, 70%);
  width: 40px;
  height: 40px; }
  .close-button button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #FF3E79;
    background-image: url("/src/img/burgerX.png");
    background-repeat: no-repeat;
    background-position: 50% 50%; }
  .close-button button:hover,
  .close-button button:focus {
    color: black;
    text-decoration: none;
    cursor: pointer; }

.warning p {
  font-family: TT Lakes;
  font-size: 14px;
  line-height: 16px;
  text-align: left; }

.warning span {
  color: #FF3E79; }

.form__btn {
  /*     border: 2px solid violet; */
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 20px 0; }
  .form__btn button {
    width: 119px;
