@charset "UTF-8";
@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

.feedback-modal {
  display: none;
  position: fixed;
  z-index: 50;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(255, 255, 255, 0.96);
  opacity: 0,5%; }

.feedback-modal-content {
  position: absolute;
  top: 0;
  right: 0px;
  width: 320px;
  box-shadow: 16px 0px 52px 0px #454F7E05;
  box-shadow: -2px 0px 4px 0px #0E185033;
  background-color: #FFFFFF; }
  .feedback-modal-content h2 {
    font-family: TT Lakes;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6000000238418579px;
    text-align: left;
    margin: 40px 0 20px 0;
    padding: 0px 0 0 15px;
    color: #1B1C21;
    border-left: 4px solid #41F6D7; }
  .feedback-modal-content form {
    display: flex;
    flex-derection: collom;
    /*     border: 2px solid gold; */
    flex-direction: column;
    padding: 0 15px 0 15px; }
    .feedback-modal-content form input {
      height: 48px;
      border: 1px solid #EAEAEA;
      border-radius: 6px;
      outline: none;
      /* Убираем стандартное выделение */
      font-family: TT Lakes;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      padding: 0 0 0 10px;
      margin: 0px 0; }
    .feedback-modal-content form input::-moz-placeholder {
      font-family: TT Lakes;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      text-align: left;
      /*       padding: 0 0 0 10px; */ }
    .feedback-modal-content form input::placeholder {
      font-family: TT Lakes;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      text-align: left;
      /*       padding: 0 0 0 10px; */ }
    .feedback-modal-content form textarea {
      resize: none;
      border: 1px solid #EAEAEA;
      border-radius: 6px;
      outline: none;
      /* Убираем стандартное выделение */
      font-family: TT Lakes;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      padding: 0 0 0 10px;
      margin: 0px 0; }
    .feedback-modal-content form textarea::-moz-placeholder {
      font-family: TT Lakes;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      text-align: left;
      padding: 10px 0 0 0px; }
    .feedback-modal-content form textarea::placeholder {
      font-family: TT Lakes;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      text-align: left;
      padding: 10px 0 0 0px; }
  .feedback-modal-content .feedback-close {
    transition: transform 1s;
    position: relative;
    transform: translate(50%, 70%);
    width: 40px;
    height: 40px; }
    .feedback-modal-content .feedback-close button {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: #FF3E79;
      background-image: url("/src/img/burgerX.png");
      background-repeat: no-repeat;
      background-position: 50% 50%; }
    .feedback-modal-content .feedback-close button:hover,
    .feedback-modal-content .feedback-close button:focus {
      color: black;
      text-decoration: none;
      cursor: pointer; }

.feedback-form-group {
  margin-bottom: 15px; }

.feedback-form-group label {
  display: block;
  margin-bottom: 5px; }

.feedback-form-group input,
.feedback-form-group textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box; }

.feedback-consent-text p {
  font-family: TT Lakes;
  font-size: 14px;
  line-height: 16px;
  text-align: left; }

.feedback-consent-text span {
  color: #FF3E79; }

