@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

.container {
  width: 100%;
  min-width: 320px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  background: #F8F8F8;
  padding-bottom: 5vh;
  padding-left: 5px; }
  .container .head-link {
    width: 100%;
    min-width: 320px;
    max-width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;
    border-top: 1px solid #D9FFF5;
    background: linear-gradient(270deg, #FFFFFF 0%, #F8F8F8 98.61%); }
    .container .head-link div {
      font-family: TT Lakes;
      font-size: 22px;
      line-height: 24px;
      letter-spacing: -0.20000000298023224px;
      margin-top: 10px;
