.container .link_flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start; }

#links {
  margin-top: 10px; }

.hidden {
  display: none; }

.visible {
  display: block; }

.container .link_flex .link > .block-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 224px;
  height: 72px;
  margin: 20px 10px;
  border: 1px solid #EAEAEA;
  border-radius: 5px;
  background-color: #fff; }
  .container .link_flex .link > .block-link .block-link__image {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    transform: translate(20px, 0px); }
  .container .link_flex .link > .block-link .block-link__btn {
    width: 40px;
    height: 40px;
    border: 2px solid #FF3E79;
    border-radius: 50%;
    background-image: url("/src/img/dropdown.png");
    background-repeat: no-repeat;
    background-position: 50%  50%;
    transform: translate(-20px, 0px); }

.container .container-link__btn {
  position: relative;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(50, 50, 50, 0);
  margin-left: 0.8em;
  padding-left: 25px;
  white-space: nowrap;
  background-image: url("/src/img/icon.png"); }

.container .container-link__btn::first-line {
  font-family: TT Lakes;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
