      justify-content: space-between;
      padding: 0 20px 0 20px; }
      .box__price .wrapper__table table thead tr th {
        font-family: TT Lakes;
        font-weight: 500;
        font-size: 16px;
        line-height: 16px;
        opacity: 0,72;
        color: #7E7E82;
        background-color: #F8F8F8;
        display: flex;
        align-items: center;
        padding: 0 0 0 0px; }
    .box__price .wrapper__table table tbody {
