      width: 30px;
      height: 24px;
      font-weight: 700;
      text-transform: uppercase;
      font-family: TT Lakes;
      font-size: 12px;
      line-height: 24px;
      letter-spacing: -0.15000000596046448px;
      color: #FFFFFF; }
    .box__price .wrapper__table__btn button p::after {
