@charset "UTF-8";
@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

.wrapper_menu {
  position: absolute; }

.burger {
  width: 40px;
  height: 40px;
  cursor: pointer;
  position: relative;
  top: 27px;
  left: 1vw;
  z-index: 16;
  background-color: pink;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center; }
  .burger button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #FF3E79;
    background-image: url("/src/img/burger.png");
    background-repeat: no-repeat;
    background-position: center; }

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.96);
  z-index: 15; }

.menu-list {
  position: relative;
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 100px 0 0 0;
  background: white;
  top: -50px;
  left: -340px;
  /* Скрываем меню за пределами экрана */
  width: 320px;
  height: auto;
  box-shadow: 16px 0px 52px 0px #0E185033;
  transition: left 0.3s ease;
  z-index: 15; }
  .menu-list .menu-item {
    display: block;
    margin: 15px 0;
    padding: 0 0 0 20px;
    text-decoration: none;
    color: black;
    transition: 0.3s ease; }
  .menu-list .menu-item:hover {
    border-left: 4px solid #41F6D7;
    color: #7E7E82; }
  .menu-list a {
    font-family: TT Lakes;
    letter-spacing: 0.2px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px; }
  .menu-list .menu__logo {
    position: absolute;
    left: 90px;
    top: 35px;
    display: flex;
    justify-content: center;
    align-items: center; }
    .menu-list .menu__logo img {
      width: 100%; }
  .menu-list .menu__searche__btn {
    position: absolute;
    right: 30px;
    top: 35px;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center; }
    .menu-list .menu__searche__btn button {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: #FF3E79;
      background-image: url("/src/img/search.png");
      background-repeat: no-repeat;
      background-position: center; }
  .menu-list .wrapper__contacts {
    padding: 20px 10px; }
    .menu-list .wrapper__contacts .contact__btns button {
      width: 40px;
      height: 40px;
      background-color: #FF3E79;
      border-radius: 50%;
      margin-left: 10px; }
    .menu-list .wrapper__contacts .contact__btns button:nth-child(1) {
      background-image: url("https://kukufc1.github.io/KaTa//src/img/call_ic.png");
      background-repeat: no-repeat;
      background-position: center;
      background-color: #FF3E79; }
    .menu-list .wrapper__contacts .contact__btns button:nth-child(2) {
      background-image: url("https://kukufc1.github.io/KaTa//src/img/chat_ic.png");
      background-repeat: no-repeat;
      background-position: center;
      background-color: #FF3E79; }
    .menu-list .wrapper__contacts .contact__btns button:nth-child(3) {
      background-image: url("https://kukufc1.github.io/KaTa//src/img/profile_ic.png");
      background-repeat: no-repeat;
      background-position: center;
      background-color: #FF3E79; }
    .menu-list .wrapper__contacts .contact__text {
      padding: 0 10px; }
      .menu-list .wrapper__contacts .contact__text p {
        font-family: TT Lakes;
        letter-spacing: 0.2px;
        font-size: 16px;
        line-height: 24px;
        margin: 10px 0 10px 0; }
      .menu-list .wrapper__contacts .contact__text p:last-child {
        font-family: TT Lakes;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.6000000238418579px;
        font-weight: 700;
        margin: 0px 0 20px 0; }
    .menu-list .wrapper__contacts .language {
      padding: 0 10px; }
      .menu-list .wrapper__contacts .language a {
        text-decoration: none;
        font-family: TT Lakes;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -.20000000298023224px;
        text-align: left;
        font-weight: 400;
        color: #000; }
      .menu-list .wrapper__contacts .language .language a:hover {
        color: #7e7e82; }

.menu-list.active {
  left: 0px;
  /* Показываем меню */ }

@media (min-width: 360px) and (max-width: 768px) {
  .menu-list {
    height: 1024px; }
    .menu-list .wrapper__contacts {
      position: absolute;
      bottom: 0; } }

@media (min-width: 769px) {
  .menu-list {
    height: 1700px; }
    .menu-list .wrapper__contacts {
      position: absolute;
      bottom: 50%; } }
