@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

.wrapper__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 88px;
  padding: 0 20px;
  transition: 0.3s ease; }
  .wrapper__header__logo {
    opacity: 1;
    display: flex;
    align-items: center; }
    .wrapper__header__logo #empty1 {
      opacity: 0;
      width: 40px;
      margin: 0 10px 0 0;
      height: 32px;
      border-right: 2px solid #EAEAEA; }
  .wrapper__header__btns {
    display: flex;
    align-items: center;
    justify-content: center; }
    .wrapper__header__btns__contacts {
      display: none;
      align-items: center; }
      .wrapper__header__btns__contacts button {
        width: 40px;
        height: 40px;
        background-color: #FF3E79;
        border-radius: 50%;
        margin: 0 10px 0 0px; }
      .wrapper__header__btns__contacts button:nth-child(1) {
        background-image: url("/src/img/call_ic.png");
        background-repeat: no-repeat;
        background-position: center; }
      .wrapper__header__btns__contacts button:nth-child(2) {
        background-image: url("/src/img/chat_ic.png");
        background-repeat: no-repeat;
        background-position: center; }
      .wrapper__header__btns__contacts button:nth-child(3) {
        background-image: url("/src/img/profile_ic.png");
        background-repeat: no-repeat;
        background-position: center; }
    .wrapper__header__btns__repair {
      display: flex;
      align-items: center; }
      .wrapper__header__btns__repair #dash2 {
        display: flex;
        width: 2px;
        height: 32px;
        border-radius: 1px;
        margin: 0 10px;
        background-color: #EAEAEA; }
      .wrapper__header__btns__repair button {
        margin: 0 0px 0 10px;
        width: 40px;
        height: 40px;
        background-color: #FF3E79;
        border-radius: 50%; }
      .wrapper__header__btns__repair #rapair1 {
        background-image: url("https://kukufc1.github.io/KaTa//src/img/repair_ic.png");
        background-repeat: no-repeat;
        background-position: center; }
      .wrapper__header__btns__repair #rapair2 {
        background-image: url("https://kukufc1.github.io/KaTa//src/img/checkstatus.png");
        background-repeat: no-repeat;
        background-position: center; }

.Title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-left: 4px solid #41F6D7;
  padding: 0 20px 0 0;
  margin: 20px 0; }
  .Title h1 {
    font-size: 28px;
    font-family: TT Lakes;
    /* text-align: left; */
    padding: 0.5em;
    white-space: nowrap; }
  .Title .title_ic_repair {
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
    transition: 0.3s ease; }
    .Title .title_ic_repair .ic_repair > button, .Title .title_ic_repair .ic_checkstatus > button {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #FF3E79;
      border-radius: 50%; }
    .Title .title_ic_repair .ic_repair_btn {
      margin: 0 100px 0 0px;
      background-image: url("https://kukufc1.github.io/KaTa//src/img/repair_ic.png");
      width: 50%;
      height: 50%;
      background-repeat: no-repeat;
      background-position: center; }
    .Title .title_ic_repair .ic_checkstatus_btn {
      background-image: url("https://kukufc1.github.io/KaTa//src/img/checkstatus.png");
      width: 50%;
      height: 50%;
      background-repeat: no-repeat;
      background-position: center; }

.title_ic_repair > div {
  display: flex;
  justify-content: center;
  align-items: center; }

.title_ic_repair > div:first-child {
  margin-right: 3em; }

.title_ic_repair > div > button > h3 {
  font-family: TT Lakes;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.20000000298023224px;
  font-weight: 525;
  white-space: nowrap;
  display: block;
  width: 150px;
  height: auto;
  transform: translate(-75%, 0%); }
