@charset "UTF-8";
/* !!!!!!!!text + img!!!!!!!!!! */
@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

.wrapper__content {
  background: #F8F8F8;
  display: flex;
  flex-direction: column;
  padding: 20px 10px 10px 10px; }
  .wrapper__content__text .text-container {
    overflow: hidden;
    max-height: 90px;
    /* Показывает только первые строки текста */
    transition: max-height 0.3s ease;
    font-family: TT Lakes;
    letter-spacing: 0.2px;
    font-size: 14px;
    line-height: 18px;
    text-align: left; }
  .wrapper__content__text .text-container.expanded {
    max-height: 500px;
    /* Достаточно большая высота для отображения всего текста */ }
  .wrapper__content__text .next_read_btn {
    background-color: #f8f8f8;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column; }
    .wrapper__content__text .next_read_btn #prgrf {
      display: block; }
    .wrapper__content__text .next_read_btn p {
      font-family: TT Lakes;
      letter-spacing: 0.2px;
      font-size: 16px;
      line-height: 24px;
      font-weight: 500; }
      .wrapper__content__text .next_read_btn p #strlchk {
        position: relative;
        top: 5px;
        display: inline-block;
        width: 20px;
        height: 20px;
        background-image: url("/src/img/icon.png");
        background-repeat: no-repeat;
        background-position: center;
        background-position: 50% 50%; }
    .wrapper__content__text .next_read_btn #prgrf1 {
      display: none; }
      .wrapper__content__text .next_read_btn #prgrf1 #strlchk1 {
        position: relative;
        top: 5px;
        display: none;
        width: 20px;
        height: 20px;
        background-image: url("/src/img/hide.png");
        background-repeat: no-repeat;
        background-position: center;
        background-position: 50% 50%; }
  .wrapper__content__img img {
    width: 100%; }
