@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

/* slider */
.swiper_box1 {
  width: 320px;
  height: 230px;
  overflow: hidden;
  background-color: #F8F8F8;
  padding-top: 20px; }
  .swiper_box1__header {
    width: 320px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 0 0 10px;
    background: linear-gradient(270deg, #FFFFFF 0%, #F8F8F8 98.61%);
    border-top: 1px solid #D9FFF5; }
    .swiper_box1__header h3 {
      font-family: TT Lakes;
      letter-spacing: 0.2px;
      font-size: 22px;
      line-height: 24px;
      font-weight: 400;
      text-align: left;
      color: #7E7E82; }

.swiper-container.slider1 {
  width: 320px;
  height: 72px;
  padding-left: 20px; }

.swiper-container.slider1 .swiper-pagination {
  position: relative;
  transform: translate(60px, 20px); }

.swiper-container.slider1 .swiper-pagination .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #B5B6BC; }

.slider1 .swiper-button-next {
  position: relative;
  width: 40px;
  height: 40px; }
  .slider1 .swiper-button-next::after {
    content: " ";
    width: 40px;
    height: 40px;
    border: 2px solid #FF3E79;
    border-radius: 50%;
    background-image: url("/src/img/dropdown.png");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    transform: translate(190px, -85px); }

.slider1 .swiper-wrapper {
  margin-top: 20px; }

.slider1 .swiper-wrapper > .swiper-slide {
  width: 240px;
  height: 72px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px; }
  .slider1 .swiper-wrapper > .swiper-slide img {
    margin-left: 10px; }
