@charset "UTF-8";
@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

@media (min-width: 320px) and (max-width: 340px) {
  .swiper_box1, .swiper_box2, .swiper_box3 {
    display: block; }
  .container, .container2, .box__price {
    display: none !important; }
  /*Cтиль будет применяться, если ширина окна браузера больше 321 пикселей*/ }

@media (min-width: 500px) and (max-width: 1120px) {
  .wrapper__header__btns__contacts {
    display: flex; }
  #empty1 {
    width: 60px !important;
    opacity: 1 !important; }
  .wrapper__content {
    width: 100%;
    flex-direction: row !important; }
    .wrapper__content__text {
      flex-basis: 50% !important; }
    .wrapper__content__img {
      flex-basis: 50% !important; }
      .wrapper__content__img img {
        width: 100%; } }

/*Cтиль будет применяться, если ширина окна браузера больше 321 пикселей но меньше 768*/
@media (min-width: 340px) and (max-width: 768px) {
  .swiper_box1, .swiper_box2, .swiper_box3 {
    display: none; }
  .container, .container2, .box__price {
    display: block !important; }
  .wrapper__table {
    padding: 0 10px 30px 10px !important; }
    .wrapper__table table th:nth-child(1), .wrapper__table table td:nth-child(1) {
      /* Первый столбец */
      min-width: 45% !important;
      height: 60px !important; }
    .wrapper__table table th:nth-child(2), .wrapper__table table td:nth-child(2) {
      /* Второй столбец */
      min-width: 25% !important;
      height: 60px !important;
      text-align: center;
      /* Выравнивание по правому краю */ }
    .wrapper__table table th:nth-child(3), .wrapper__table table td:nth-child(3) {
      /* Третий столбец */
      min-width: 20% !important;
      height: 60px !important;
      text-align: center;
      /* Выравнивание по центру */ }
    .wrapper__table table th:nth-child(4), .wrapper__table table td:nth-child(4) {
      /* Четвертый столбец */
      min-width: 10% !important;
      height: 60px !important; }
  .wrapper__table__btn button {
    width: 30px !important;
    height: 30px; }
    .wrapper__table__btn button p {
      visibility: hidden; }
    .wrapper__table__btn button p::after {
      transform: translate(0px, -25px) !important; }
  .footer {
    width: 100% !important;
    border-top: 1px solid #D9FFF5;
    background-color: #F8F8F8; }
    .footer__wrapper {
      display: flex;
      justify-content: space-around; }
      .footer__wrapper__author {
        height: auto;
        flex-basis: 30%;
        margin-bottom: 0px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start; }
        .footer__wrapper__author p {
          text-align: left; }
      .footer__wrapper__rules {
        height: 100px;
        flex-basis: 70%;
        display: flex;
        justify-content: center; }
        .footer__wrapper__rules p {
          flex-basis: 45%;
          text-align: right; }
        .footer__wrapper__rules p:first-child {
          text-align: center;
          flex-basis: 55%;
          margin-bottom: 0px; }
  /*Cтиль будет применяться, если ширина окна браузера больше 321 пикселей*/ }

@media (min-width: 321px) and (max-width: 768px) {
  .title_ic_repair > div {
    display: none; }
  /*Cтиль будет применяться, если ширина окна браузера больше 321 пикселей*/ }

@media (min-width: 769px) {
  .swiper_box1, .swiper_box2, .swiper_box3 {
    display: none !important; }
  .container, .container2, .box__price {
    display: block !important; }
  .footer {
    width: 100% !important;
    border-top: 1px solid #D9FFF5;
    background-color: #F8F8F8; }
    .footer__wrapper {
      display: flex;
      justify-content: space-around; }
      .footer__wrapper__author {
        height: auto;
        flex-basis: 30%;
        margin-bottom: 0px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start; }
        .footer__wrapper__author p {
          text-align: left; }
      .footer__wrapper__rules {
        height: 100px;
        flex-basis: 70%;
        display: flex;
        justify-content: center; }
        .footer__wrapper__rules p {
          flex-basis: 45%;
          text-align: right; }
        .footer__wrapper__rules p:first-child {
          text-align: center;
          flex-basis: 55%;
          margin: 0px 0% 0 0; }
  .modal-content {
    width: 440px !important; }
    .modal-content h2 {
      margin: 45px 0 0 0 !important; }
  .close-button {
    position: absolute !important;
    transform: translate(-150%, 90%) !important; }
  .feedback-modal-content {
    width: 440px !important; }
    .feedback-modal-content h2 {
      padding: 5px 10px !important;
      margin: 0px 0 20px 0 !important; }
    .feedback-modal-content .feedback-close {
      transform: translate(-150%, 90%) !important; }
  /*Cтиль будет применяться, если ширина окна браузера больше 769 пикселей*/ }

@media (min-width: 1120px) {
  .wrapper__header {
    height: 60px;
    transition: 0.3s ease; }
    .wrapper__header__logo {
      opacity: 0;
      transition: 0.3s ease; }
    .wrapper__header__btns {
      opacity: 0;
      transition: 0.3s ease; }
  .swiper_box1, .swiper_box2, .swiper_box3 {
    display: none; }
  .title_ic_repair {
    opacity: 1 !important;
    display: flex  !important; }
  .wrapper__content {
    width: 100% !important;
    flex-direction: row !important; }
    .wrapper__content__text {
      flex-basis: 50% !important; }
    .wrapper__content__img {
      flex-basis: 50% !important; }
      .wrapper__content__img img {
        width: 100% !important; }
  /*Cтиль будет применяться, если ширина окна браузера больше 1120 пикселей*/ }
