@font-face {
  font-family: 'Comic';
  src: url(./fonts/comic.ttf) format("truetype");
  font-weight: normal;
  font-style: italic; }

/* slider3 */
.swiper_box3 {
  width: 320px;
  padding-bottom: 30px;
  overflow: hidden;
  background-color: #F8F8F8; }
  .swiper_box3__header {
    width: 320px;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0px 0 0 10px;
    background: linear-gradient(270deg, #FFFFFF 0%, #F8F8F8 98.61%);
    border-top: 1px solid #D9FFF5; }
    .swiper_box3__header h3 {
      font-family: TT Lakes;
      letter-spacing: 0.2px;
      font-size: 22px;
      line-height: 24px;
      font-weight: 400;
      text-align: left;
      color: #7E7E82; }
  .swiper_box3 .info {
    height: 191px;
    padding: 0px 10px; }
    .swiper_box3 .info .content {
      width: 288px;
      height: 137px; }
      .swiper_box3 .info .content p {
        font-family: TT Lakes;
        letter-spacing: 0.2px;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        text-align: left;
        color: #1B1C21; }
    .swiper_box3 .info .offer {
      width: 202px;
      height: 54px; }
      .swiper_box3 .info .offer a {
        font-family: TT Lakes;
        letter-spacing: 0.2px;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        text-align: left;
        color: #1B1C21; }
        .swiper_box3 .info .offer a::after {
          content: " ";
          display: block;
          position: relative;
          top: -45%;
          left: 55%;
          width: 24px;
          height: 24px;
          background-image: url("/src/img/dropdown.png");
          background-repeat: no-repeat;
          background-position: 50% 50%; }

.swiper-container.slider3 {
  padding-top: 20px; }
  .swiper-container.slider3 .btn_order {
    width: 125px;
    height: 32px;
    position: relative;
    z-index: 2;
    transform: translate(130px, -65px); }
    .swiper-container.slider3 .btn_order button {
      width: 125px;
      height: 32px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      border-radius: 16px 16px 16px 16px;
      padding: 10px;
      background-color: #FF3E79; }
      .swiper-container.slider3 .btn_order button p {
        display: block;
        width: 30px;
        height: 24px;
        font-weight: 700;
        text-transform: uppercase;
        font-family: TT Lakes;
        font-size: 12px;
        line-height: 24px;
        letter-spacing: -0.15000000596046448px;
        color: #FFFFFF; }
      .swiper-container.slider3 .btn_order button p::after {
        content: " ";
        display: block;
        position: relative;
        top: -100%;
        left: 290%;
        width: 24px;
        height: 24px;
        background-image: url("/src/img/dropdown.png");
        filter: brightness(0) invert(1);
        background-repeat: no-repeat;
        background-position: 50% 50%; }

.swiper-slide__box {
  width: 260px;
  height: 200px;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  padding: 20px 0 0 10px;
  margin-left: 10px; }
  .swiper-slide__box .service, .swiper-slide__box .price, .swiper-slide__box .period {
    margin-bottom: 10px;
    overflow: hidden; }
    .swiper-slide__box .service h5, .swiper-slide__box .price h5, .swiper-slide__box .period h5 {
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      text-align: left;
      color: #7E7E82;
      font-family: TT Lakes;
      letter-spacing: 0px;
      opacity: 72%; }

.swiper-container1 p, .swiper-container2 p, .swiper-container3 p {
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  text-align: left;
  color: #1B1C21;
  font-family: TT Lakes;
  letter-spacing: 0.2px; }

.swiper-container.slider3 .swiper-pagination {
  position: relative;
  transform: translate(0px, -10px); }
  .swiper-container.slider3 .swiper-pagination span {
    margin: 0 5px; }

.swiper-container.slider3 .swiper-pagination .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #B5B6BC; }
